<template>
  <Bar
        v-if="chartDataReady"
        id="my-chart-id"
        :options="chartOptions"
        :data="chartData"
        ref="myChart"
      />
  </template>
  
  <script>
  import { Bar } from 'vue-chartjs'
  import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale, LineElement, PointElement } from 'chart.js'
  
  ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale, LineElement, PointElement)
  
  export default {
    name: 'InvoiceAIPerformance',
    components: { Bar },
    data() {
      return {
        chartData: {
          labels: [], // Initialize as an empty array
          datasets: [
            {
              label: 'Average File Size',
              backgroundColor: 'rgba(135, 206, 235, 0.5)', // Skyblue color with 50% opacity
              data: [], // Initialize as an empty array
              yAxisID: 'y', // Bind this dataset to the first y-axis
              order: 1 // Draw the bar chart first
            },
            {
              label: 'Average Processing Time',
              backgroundColor: 'orange',
              data: [], // Initialize as an empty array
              type: 'line',
              borderColor: 'orange',
              fill: false,
              yAxisID: 'y1', // Bind this dataset to the second y-axis
              order: 2 // Draw the line chart after the bar chart
            }
          ]
        },
        chartOptions: {
          responsive: true,
          scales: {
            x: {
              title: {
                display: true,
                text: 'Date',
              },
            },
            y: {
              title: {
                display: true,
                text: 'File Size',
              },
              beginAtZero: true,
            },
            y1: { // Define a second y-axis
              position: 'right', // Position this axis on the right
              grid: {
                drawOnChartArea: false, // Optional: If you don't want grid lines on this axis
              },
              title: {
                display: true,
                text: 'Processing Time',
              },
              beginAtZero: true,
            },
          },
          plugins: {
            title: {
              display: true,
              text: 'Invoice AI Performance - Past 5 days',
              font: {
                size: 18, // Title font size
              },
              padding: {
                top: 10,
                bottom: 20,
              }
            },
            legend: {
              position: 'top',
            },
          },
        },
        chartDataReady: false // Add a flag to control the rendering
      }
    },
    async mounted() {
      await this.fetchData(); // Fetch data when component mounts
    },
    methods: {
      async fetchData() {
        try {
          const response = await fetch(`${process.env.VUE_APP_API_BASE_URL}invoice-ai-performance-metrics`);
          const data = await response.json();
          console.log('Performance metrics:', data);
  
          if (data && data.length > 0) {
            // Format dates to a more readable format
            this.chartData.labels = data.map((entry) => new Date(entry.processing_date).toLocaleDateString('en-US'));
            this.chartData.datasets[0].data = data.map((entry) => entry.average_file_size);
            this.chartData.datasets[1].data = data.map((entry) => entry.average_processing_time);
  
            // Set the flag to true to trigger the chart rendering
            this.chartDataReady = false;
            this.$nextTick(() => {
              this.chartDataReady = true;
            });
          }
          console.log('Updated chart data:', this.chartData);
        } catch (error) {
          console.error('Error fetching performance metrics:', error);
        }
      }
    }
  }
  </script>
  