            kind_of_package: 'CT',
            marks_and_numbers: `CHASSIS # ${detail.chassisNumber}`,
            chassis_number: detail.chassisNumber,
            curb_weight: vehicle.curbWeight,
            engine_number: detail.engineNumber,
